<template>
	<div id="table-pindan">
		<public-header title="同桌点餐"></public-header>
		<div class="content">
			<template v-if="extra.is_founder == 1 && pindan.mine.pindan_status == 2">
				<div class="lock">
					<div class="lock-inner w-100 text-center">
						<div class="tip font-15">锁定状态，手动解锁后可以继续点餐</div>
						<van-button size="normal" block class="bg-danger font-16 deblock" @click="onContinue">解锁</van-button>
					</div>
				</div>
			</template>
			<template v-else>
				<div class="content-inner">
					<van-pull-refresh v-model="isRefresh" @refresh="onPullDownRefresh">
					<!--商家信息-->
					<div class="store bg-default span-center padding-15">
						<img :src="store.logo" />
						<div class="name ellipsis">{{store.title}} {{table.category.title}}~{{table.title}}桌</div>
						<template v-if="pindan.pindan_status == 1">
							<van-button size="normal" block class="bg-danger font-16" v-if="0 && extra.is_founder == 1" @click="onChangeZhezhao">邀请好友</van-button>
							<van-button size="normal" block class="bg-danger font-16" v-if="extra.not_takepart == 1" @click="onTakePindan">参与点餐</van-button>
						</template>
						<template v-else-if="pindan.pindan_status == 2">
							<div class="tip font-15">正在提交订单中...</div>
						</template>
						<template v-else-if="pindan.pindan_status == 3">
							<div class="tip font-15">订单已提交成功</div>
						</template>
					</div>
					<template v-if="pindan.mine">
						<div class="font-14 c-gray margin-10">点餐详情</div>
						<div class="order-info bg-default padding-10-l">
							<div class="user flex-lr padding-10-t" v-if="pindan.mine.member">
								<div class="left flex">
									<img class="avatar" :src="pindan.mine.member.avatar">
								</div>
								<div class="right flex-lr van-hairline--bottom">
									<div class="nickname">
										<span class="ellipsis">{{pindan.mine.member.nickname}}</span>
										<span class="label label-mine" >我</span>
										<span class="label label-start" v-if="pindan.mine.id == pindan.pindan_id">发起</span>
									</div>
									<div class="btn-group flex" v-if="pindan.pindan_status == 1">
										<div class="btn-item edit-btn" @click="onEditGoods">编辑商品</div>
										<div class="btn-item cancle-btn" @click="onGiveUp(pindan.mine.id, 0)">不点了</div>
									</div>
								</div>
							</div>
							<div class="goods flex-lr" v-if="pindan.mine.data">
								<div class="left"></div>
								<div class="right">
									<template v-for="item in pindan.mine.data">
										<div class="goods-item" v-for="option in item" :key="option.id">
											<div class="goods-title ellipsis">{{option.title}}</div>
											<div class="goods-num">x{{option.num}}</div>
											<div class="goods-price c-danger">¥{{option.total_discount_price}}</div>
										</div>
									</template>
								</div>
							</div>
						</div>
					</template>
					<template v-if="pindan.other && pindan.other.length > 0">
						<div class="font-14 c-gray margin-10">其他桌友</div>
						<div class="order-info bg-default padding-10-l margin-10-b" v-for="cart in pindan.other" :key="cart.id">
							<div class="user flex-lr padding-10-t">
								<div class="left flex">
									<img class="avatar" :src="cart.member.avatar">
								</div>
								<div class="right flex-lr van-hairline--bottom">
									<div class="nickname">
										<span class="ellipsis">{{cart.member.nickname}}</span>
										<span class="label label-start" v-if="cart.id == pindan.pindan_id">发起</span>
									</div>
									<i class="icon icon-delete margin-10-r font-18 c-gray" @click="onGiveUp(cart.id, 1)" v-if="extra.is_founder"></i>
								</div>
							</div>
							<div class="goods flex-lr">
								<div class="left"></div>
								<div class="right">
									<template v-for="item in cart.data">
										<div class="goods-item" v-for="option in item" :key="option.id">
											<div class="goods-title ellipsis">{{option.title}}</div>
											<div class="goods-num">x{{option.num}}</div>
											<div class="goods-price c-danger">¥{{option.total_discount_price}}</div>
										</div>
									</template>
								</div>
							</div>
						</div>
					</template>
					<van-cell-group class="margin-10-t">
						<van-cell title="商品费用">
							<span slot="right-icon" class="c-danger">
								¥{{pindan.price}}
							</span>
						</van-cell>
					</van-cell-group>
					</van-pull-refresh>
				</div>
				<div class="cart flex-lr" v-if="extra.is_founder == 1">
					<div class="takepart-tips" v-if="pindan.takepart_num > 1">
						共<span class="c-danger">{{pindan.takepart_num}}</span>人在同时点餐
					</div>
					<div class='cart-tips'>
						<div class="total font-bold">¥{{pindan.price}}</div>
					</div>
					<template v-if="pindan.num > 0">
						<div class='cart-btn' @click="onSubmit">去结算</div>
					</template>
					<template v-else>
						<div class="cart-btn disabled">未选择商品</div>
					</template>
				</div>
			</template>
		</div>
		<transition name="loading">
			<iloading v-if="showPreLoading"></iloading>
		</transition>
	</div>
</template>

<script>
	import PublicHeader from '@/components/header'
	import { Dialog } from 'vant';
	import {getData, setData} from "@/controller/funcCommon"
	export default {
		components: {
			PublicHeader,
			Dialog
		},
		data() {
			return {
				showPreLoading: true,
				isRefresh: false,
				zhezhaoShow: false,
				pindan: {
					mine: {},
				},
				store: {
					cn: {}
				},
				table: {
					category: {}
				},
				extra: {},
				goback: true
			}
		},
		methods: {
			onPullDownRefresh() {
				this.onLoad();
			},
			onLoad() {
				var that = this;
				getData({
					vue: that,
					url: 'wmall/store/table/pindan',
					data: {
						sid: that.sid,
						pindan_id: that.pindan_id,
						table_id: that.table_id,
					},
					success: function(res) {
						that.pindan = res.pindan;
						that.store = res.store;
						that.extra = res.extra;
						that.isRefresh = false;
						that.pindan_id = that.pindan.pindan_id;
						that.table = res.table;
						// 按需使用：A→B→C就需要页面一进来的时候，就添加一个历史记录
						window.history.pushState(null, null, document.URL);
						// 给window添加一个popstate事件，拦截返回键，执行this.onBrowserBack事件，addEventListener需要指向一个方法
						window.addEventListener("popstate", that.onBrowserBack, false);
						//window.removeEventListener("popstate", that.onBrowserBack, false);
					},
					fail: function(res) {
						that.util.$toast(res.message, that.util.getUrl({path: '/tangshi/pages/table/goods', query: {sid: that.sid, table_id: that.table_id}}), 1000, 'replace');
					}
				})
			},
			onEditGoods() {
				var that = this;
				that.$router.replace(that.util.getUrl({path: 'tangshi/pages/table/goods', query: {sid: that.store.id, table_id: that.table_id, pindan_id: that.pindan_id}}))
			},
			onGiveUp(cart_id, type) {
				var that = this;
				var confirm = '确定不要继续点餐了吗？';
				if(type == 1) {
					confirm = '删除后不可恢复，确认删除吗？';
				}
				setData({
					vue: that,
					confirm: confirm,
					url: 'wmall/store/table/giveupPindan',
					data: {
						sid: that.sid,
						cart_id: cart_id,
						table_id: that.table_id
					},
					success: function(res) {
						that.pindan = res.pindan;
						that.extra.not_takepart = res.extra.not_takepart;
						if(cart_id == that.pindan_id) {
							that.util.$toast('取消点餐成功, 继续参与点餐请再次扫码', that.util.getUrl({path: 'pages/store/index', query: {sid: that.sid}}), 1000, 'replace');
						} else {
							that.util.$toast('取消点餐成功', '', 1000);
						}
					},
					fail: function(res) {
						if(res.errno == -1000) {
							that.onGoBack();
						}
					}

				})
			},
			onContinue(cart_id) {
				var that = this;
				setData({
					vue: that,
					url: 'wmall/store/table/continuePindan',
					data: {
						sid: that.sid,
						table_id: that.table_id
					},
					success: function(res) {
						that.onLoad();
					}
				})
			},
			onTakePindan() {
				var that = this;
				that.$router.replace(that.util.getUrl({'path': 'tangshi/pages/table/goods', query: {sid: that.sid, table_id: that.table_id}}));
			},
			onSubmit() {
				Dialog.confirm({
					title: '温馨提示',
					message: '有其他桌友正与你一起点餐，确定去结算吗？'
				}).then(() => {
					this.$router.push(this.util.getUrl({'path': 'tangshi/pages/table/create', query: {sid: this.store.id, table_id: this.table_id, is_pindan: 1, pindan_id: this.pindan.pindan_id}}));
				})
			},
			onGoBack() {
				var that = this;
				if(window.history.length < 2) {
					that.$router.replace(that.util.getUrl({path: '/pages/store/index', query: {sid: that.sid}}));
				} else {
					that.goback = false;
					that.$router.replace(that.util.getUrl({path: '/pages/store/index', query: {sid: that.sid}}));
					//that.$router.back();
				}
			},
			onBrowserBack() {
				var that = this;
				if(!that.goback) {
					that.onGoBack();
					return false;
				}
				this.goback = false;
				Dialog.confirm({
					title: '你确定退出拼单吗',
					message: '退出拼单不会保留此次拼单内容'
				}).then(() => {
					setData({
						vue: that,
						url: 'wmall/store/table/giveupPindan',
						data: {
							sid: that.sid,
							cart_id: that.pindan_id,
							table_id: that.table_id
						},
						success: function(res) {
							that.onGoBack();
						},
						fail: function(res) {
							if(res.errno == -1000) {
								that.onGoBack();
							}
						}

					})
				}).catch(() => {
					this.goback = true;
					window.history.pushState(null, null, document.URL);
				})
			}
		},
		created() {
			this.query = this.$route.query;
			this.sid = this.query.sid;
			this.pindan_id = this.query.pindan_id;
			this.table_id = this.query.table_id;
		},
		mounted() {
			this.onLoad();
		},
		beforeDestroy() {
			window.removeEventListener("popstate", this.onBrowserBack, false);
			return false;
		}
	}
</script>

<style>
	#table-pindan .content .content-inner{
		position: absolute;
		top: 0;
		left: 0;
		bottom: 0px;
		right: 0;
		
		overflow-y: scroll;
		-webkit-overflow-scrolling: touch;
		background-color: #f5f5f5;
		padding-bottom: 82px;
	}
	#table-pindan .content .cart{
		position: absolute;
		bottom: 0;
		left: 0;
		width: 100%;
		height: 50px;
		background-color: #313131;
	}
	#table-pindan .content .cart .takepart-tips{
		position: absolute;
		top: -32px;
		right: 0;
		left: 0;
		height: 32px;
		line-height: 32px;
		text-align: center;
		background-color: #fff7cc;
		font-size: 12px;
	}
	#table-pindan .content .cart .cart-tips{
		height: 50px;
		flex: 1;
		color: #fff;
		display: flex;
		flex-flow: column nowrap;
		align-items: flex-start;
		justify-content: center;
		padding-left: 15px;
	}
	#table-pindan .content .cart .cart-tips .delivery-fee{
		font-size: 12px;
		color: #747474;
		margin-top: 3px;
	}
	#table-pindan .content .cart  .cart-btn{
		width: 110px;
		height: 50px;
		background-color: #ff2d4b;
		color: #fff;
		display: flex;
		align-items: center;
		justify-content: center;
		font-weight: bold;
	}
	#table-pindan .content .cart .cart-btn.disabled{
		background-color: #313131!important;
		color: #999;
	}
	/*商户信息*/
	#table-pindan .store{
		display: flex;
		flex-flow: column nowrap;
		align-items: center;
		justify-content: center;
	}
	#table-pindan .store img{
		width: 55px;
		height: 55px;
		border-radius: 100%;
	}
	#table-pindan .store .name{
		max-height: 90%;
		margin: 0 auto;
		font-size: 15px;
		font-weight: 600;
		margin: 10px 0;
	}
	#table-pindan .store .join{
		height: 45px;
		line-height: 45px;
		background: #ff2d4b;
		color: #fff;
		border-radius: 2px;
		margin: 10px 0 10px 0;
		font-size: 16px;
	}

	/*成员信息*/
	#table-pindan .order-info .left{
		width: 11%;
	}
	#table-pindan .order-info .right{
		width: 86%;
	}
	#table-pindan .order-info .user .right{
		min-height: 45px;
	}
	#table-pindan .order-info .user .avatar{
		width: 100%;
		border-radius: 100%;
		border: 1px solid #eee;
	}
	#table-pindan .order-info .user .nickname{
		display: flex;
		align-items: center;
		font-size: 14px;
		color: #000;
		width: 55%;
	}
	#table-pindan .order-info .user .nickname span:first-child{
		display: inline-block;
		font-weight: 600;
		max-width: 60%;
	}
	#table-pindan .order-info .btn-group .btn-item{
		border-radius: 2px;
		font-size: 11px;
		padding: 2px 5px;
		margin-right: 10px;
	}
	#table-pindan .order-info .btn-group .btn-item.edit-btn{
		border: 1px solid #F4BC5D;
		color: #E2BC59;
	}
	#table-pindan .order-info .btn-group .btn-item.cancle-btn{
		border: 1px solid #BCBCBC;
		color: #AEAEAE;
	}
	#table-pindan .order-info .goods .goods-item{
		width: 100%;
		display: flex;
		flex-flow: row nowrap;
		align-items: center;
		color: #717171;
		font-size: 14px;
		margin: 10px 0;
	}
	#table-pindan .order-info .goods .goods-item .goods-title{
		width: 70%;
		padding-right: 20px;
		box-sizing: border-box;
	}
	#table-pindan .order-info .goods .goods-item .goods-num{
		width: 10%;
	}
	#table-pindan .order-info .goods .goods-item .goods-price{
		width: 20%;
		box-sizing: border-box;
		padding-right: 10px;
		text-align: right;
	}
	/*用户昵称标签*/
	#table-pindan .order-info .label{
		height: 14px;
		line-height: 14px;
		font-size: 9px;
		border-radius: 2px;
		padding: 0px 4px;
		margin-left: 5px;
	}
	#table-pindan .order-info .label.label-mine{
		color: #F8D594;
		border: 1px solid #F8D594;
	}
	#table-pindan .order-info .label.label-start{
		color: #4987DC;
		border: 1px solid #4987DC;
	}

	/*锁定状态*/
	#table-pindan .content .lock{
		width: 100%;
		height: 100%;
		display: flex;
		align-items: center;
		justify-content: center;
	}
	#table-pindan .content .deblock{
		width: 80%;
		margin: 10px auto 0;
	}

</style>


